.chat-form {
  height: 7vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.message-input {
  height: 7vh;
  width: 85%;
  font-size: 16px;
  outline: none;
}
.message-submit-button {
  height: 7vh;
  width: 15%;
  font-size: 16px;
  background-color: rgb(3, 148, 3);
  color: white;
  cursor: pointer;
  border: none;
}
@media screen and (max-height: 500px) {
  .chat-form {
    height: 10vh;
  }
  .message-input {
    height: 10vh;
  }
  .message-submit-button {
    height: 10vh;
  }
}
